iframe{

    overflow: scroll;
  }
  
  .puerto-menu-sub {
   list-style: none;
   padding: 0;
   margin: 0;
   background: #FFF;
   border-radius: 2px;
   -moz-border-radius: 2px;
   -webkit-border-radius: 2px;
  float: left;
   width: 250px;
  }
  
  
  
  .viewprod{
  
  margin-left: 30%;
  margin-right: 0%;
  border-right: 0%;
  
  }
  .puerto-menu-sub li {
   position: relative;
  }
  .puerto-menu-sub li a {
   display: block;
   text-decoration: none;
   padding: 10px 5px;
   color: #777;
   text-align: left;
   font-family: OpenSans;
   height: 36px;
   position: relative;
   border-bottom: 1px solid #EEE;
  }
  .puerto-menu-sub li a i {
   float: left;
   font-size: 20px;
   margin: 0 10px 0 0;
  }
  .puerto-menu-sub li a p {
   float: left;
   margin: 0 ;
  }
  
  .puerto-menu-sub li a strong {
   display: block;
   text-transform: uppercase;
  }
  .puerto-menu-sub li a small {
   display: block;
   font-size: 10px;
  }
  
  .puerto-menu-sub li a i, .puerto-menu-sub li a strong, .puerto-menu-sub li a small {
   position: relative;
  
   transition: all 300ms linear;
   -o-transition: all 300ms linear;
   -ms-transition: all 300ms linear;
   -moz-transition: all 300ms linear;
   -webkit-transition: all 300ms linear;
  }
  .puerto-menu-sub li:hover > a i {
     opacity: 1;
     -webkit-animation: moveFromTop 300ms ease-in-out;
     animation: moveFromTop 300ms ease-in-out;
  }
  .puerto-menu-sub li:hover a strong {
     opacity: 1;
     -webkit-animation: moveFromLeft 300ms ease-in-out;
     animation: moveFromLeft 300ms ease-in-out;
  }
  .puerto-menu-sub li:hover a small {
     opacity: 1;
     -webkit-animation: moveFromRight 300ms ease-in-out;
     animation: moveFromRight 300ms ease-in-out;
  }
  
  .puerto-menu-sub li:hover > a {
   color: #e67e22;
  }
  .puerto-menu-sub li a.menu-active {
   position: relative;
   color: #e67e22;
   border:0;
  
   box-shadow: 0 0 5px #DDD;
   -moz-box-shadow: 0 0 5px #DDD;
   -webkit-box-shadow: 0 0 5px #DDD;
  
   border-left: 4px solid #e67e22;
   border-right: 4px solid #e67e22;
   margin: 0 -4px;
  }
  .puerto-menu-sub li a.active:before {
   content: "";
   position: absolute;
   top: 42%;
   left: 0;
   border-left: 5px solid #e67e22;
   border-top: 5px solid transparent;
   border-bottom: 5px solid transparent;
  }
  
  .puerto-menu-sub li a.active:after {
   content: "";
   position: absolute;
   top: 42%;
   right: 0;
   border-right: 5px solid #e67e22;
   border-top: 5px solid transparent;
   border-bottom: 5px solid transparent;
  }
  
  @-webkit-keyframes moveFromTop {
     from {
         opacity: 0;
         -webkit-transform: translateY(200%);
         -ms-transform: translateY(200%);
         transform: translateY(200%);
     }
     to {
         opacity: 1;
         -webkit-transform: translateY(0%);
         -ms-transform: translateY(0%);
         transform: translateY(0%);
     }
  }
  @-webkit-keyframes moveFromLeft {
     from {
         opacity: 0;
         -webkit-transform: translateX(200%);
         -ms-transform: translateX(200%);
         transform: translateX(200%);
     }
     to {
         opacity: 1;
         -webkit-transform: translateX(0%);
         -ms-transform: translateX(0%);
         transform: translateX(0%);
     }
  }
  @-webkit-keyframes moveFromRight {
     from {
         opacity: 0;
         -webkit-transform: translateX(-200%);
         -ms-transform: translateX(-200%);
         transform: translateX(-200%);
     }
     to {
         opacity: 1;
         -webkit-transform: translateX(0%);
         -ms-transform: translateX(0%);
         transform: translateX(0%);
     }
  }
  
  
  
  
  
  .puerto-menu-sub li ul,
  .puerto-menu-sub li ul li ul {
   list-style: none;
   position: absolute;
   height: auto;
   min-width: 200px;
   padding: 0;
   margin: 0;
   background: #FFF;
   opacity: 0;
   visibility: hidden;
   transition: all 300ms linear;
   -o-transition: all 300ms linear;
   -ms-transition: all 300ms linear;
   -moz-transition: all 300ms linear;
   -webkit-transition: all 300ms linear;
   z-index: 1000;
  
   left:280px;
   top: 0px;
   border-left: 4px solid #e67e22;
  }
  .puerto-menu-sub li ul:before {
   content: "";
   position: absolute;
   top: 25px;
   left: -9px;
   border-right: 5px solid #e67e22;
   border-bottom: 5px solid transparent;
   border-top: 5px solid transparent;
  }
  .puerto-menu-sub li:hover > ul,
  .puerto-menu-sub li ul li:hover > ul {
   display: block;
   opacity: 1;
   visibility: visible;
   left:250px;
  }
  .puerto-menu-sub li ul li a {
   padding: 10px;
   text-align: left;
   border: 0;
   border-bottom: 1px solid #EEE;
   height: auto;
  }
  .puerto-menu-sub li ul li a i {
   font-size: 16px;
   display: inline-block;
   margin: 0 10px 0 0;
  }
  .puerto-menu-sub li ul li ul {
   left: 230px;
   top: 0;
   border: 0;
   border-left: 4px solid #e67e22;
  }
  .puerto-menu-sub li ul li ul:before {
   content: "";
   position: absolute;
   top: 15px;
   left: -9px;
   border-right: 5px solid #e67e22;
   border-bottom: 5px solid transparent;
   border-top: 5px solid transparent;
  }
  .puerto-menu-sub li ul li:hover > ul {
   top: 0px;
   left: 200px;
  }
  
  .puerto-menu-sub li a.search {
   padding: 10px 10px 15px 10px;
   clear: both;
  }
  .puerto-menu-sub li a.search i {
   margin: 0;
   display: inline-block;
   font-size: 18px;
  }
  .puerto-menu-sub li a.search input {
   border: 1px solid #EEE;
   padding: 10px;
   background: #FFF;
   outline: none;
   color: #777;
   width:170px;
   float:left;
  }
  .puerto-menu-sub li a.search button {
   border: 1px solid #e67e22;
   background: #e67e22;
   outline: none;
   color: #FFF;
   margin-left: -4px;
   float:left;
   padding: 10px 10px 11px 10px;
  }
  .puerto-menu-sub li a.search input:focus {
   border: 1px solid #e67e22;
  }
  
  
  .search-mobile {
   display:none !important;
   background:#e67e22;
   border-left:1px solid #e67e22;
   border-radius:0 3px 3px 0;
  }
  .search-mobile i {
   color:#FFF;
   margin:0 !important;
  }
  
  
  @media only screen and (min-width: 960px) and (max-width: 1199px) {
     .puerto-menu-sub {
     margin-left:10px;
   }
  }
  
  @media only screen and (min-width: 768px) and (max-width: 959px) {
     .puerto-menu-sub {
     width: 200px;
   }
   .puerto-menu-sub li a {
     height:30px;
   }
   .puerto-menu-sub li a i {
     font-size: 22px;
   }
   .puerto-menu-sub li a strong {
     font-size: 12px;
   }
   .puerto-menu-sub li a small {
     font-size: 10px;
   }
   .puerto-menu-sub li a.search input {
     width: 120px;
     font-size: 12px;
   }
   .puerto-menu-sub li a.search buton{
     padding: 8px 10px 9px 10px;
   }
   .puerto-menu-sub li > ul {
     min-width:180px;
   }
   .puerto-menu-sub li:hover > ul {
     min-width:180px;
     left:200px;
   }
   .puerto-menu-sub li ul li > ul, .puerto-menu-sub li ul li ul li > ul {
     min-width:150px;
   }
   .puerto-menu-sub li ul li:hover > ul {
     left:180px;
     min-width:150px;
   }
   .puerto-menu-sub li ul li ul li:hover > ul {
     left:150px;
     min-width:150px;
   }
   .puerto-menu-sub li ul a {
     font-size:12px;
   }
   .puerto-menu-sub li ul a i {
     font-size:14px;
   }
  }
  
  @media only screen and (min-width: 480px) and (max-width: 767px) {
  
   .puerto-menu-sub {
     width: 50px;
   }
   .puerto-menu-sub li a {
     position: relative;
     padding: 12px 16px;
     height:20px;
   }
   .puerto-menu-sub li a small {
     display: none;
   }
   .puerto-menu-sub li a strong {
     display: none;
   }
   .puerto-menu-sub li a:hover strong,.puerto-menu-sub li a.active strong {
     display:block;
     font-size:10px;
     padding:3px 0;
     position:absolute;
     bottom:0px;
     left:0;
     background:#e67e22;
     color:#FFF;
     min-width:100%;
     text-transform:lowercase;
     font-weight:normal;
     text-align:center;
   }
   .puerto-menu-sub li .search {
     display: none;
   }
  
   .puerto-menu-sub li > ul {
     min-width:180px;
     left:70px;
   }
   .puerto-menu-sub li:hover > ul {
     min-width:180px;
     left:50px;
   }
   .puerto-menu-sub li ul li > ul, .puerto-menu-sub li ul li ul li > ul {
     min-width:150px;
   }
   .puerto-menu-sub li ul li:hover > ul {
     left:180px;
     min-width:150px;
   }
   .puerto-menu-sub li ul li ul li > ul {
     left:35px;
     top: 45px;
     border:0;
     border-top:4px solid #e67e22;
   }
   .puerto-menu-sub li ul li ul li > ul:before {
     left:30px;
     top: -9px;
     border:0;
     border-bottom:5px solid #e67e22;
     border-left:5px solid transparent;
     border-right:5px solid transparent;
   }
   .puerto-menu-sub li ul li ul li:hover > ul {
     left:30px;
     min-width:150px;
     top: 35px;
   }
   .puerto-menu-sub li ul a {
     font-size:12px;
   }
   .puerto-menu-sub li ul a i {
     font-size:14px;
   }
  
  }
  
  @media only screen and (max-width: 479px) {
     .puerto-menu-sub {
     width: 50px;
   }
   .puerto-menu-sub li a {
     position: relative;
     padding: 12px 16px;
     height:20px;
   }
   .puerto-menu-sub li a small {
     display: none;
   }
   .puerto-menu-sub li a strong {
     display: block;
     font-size: 5px;
   }
   .puerto-menu-sub li a:hover strong,.puerto-menu-sub li a.active strong {
     display:block;
     font-size:10px;
     padding:3px 0;
     position:absolute;
     bottom:0px;
     left:0;
     background:#e67e22;
     color:#FFF;
     min-width:100%;
     text-transform:lowercase;
     font-weight:normal;
     text-align:center;
   }
   .puerto-menu-sub li .search {
     display: none;
   }
  
   .puerto-menu-sub li > ul {
     min-width:180px;
     left:70px;
   }
   .puerto-menu-sub li:hover > ul {
     min-width:180px;
     left:50px;
   }
   .puerto-menu-sub li ul li > ul, .puerto-menu-sub li ul li ul li > ul {
     min-width:150px;
   }
   .puerto-menu-sub li ul li:hover > ul {
     left:180px;
     min-width:150px;
   }
   .puerto-menu-sub li ul li ul li > ul {
     left:35px;
     top: 45px;
     border:0;
     border-top:4px solid #e67e22;
   }
   .puerto-menu-sub li ul li ul li > ul:before {
     left:30px;
     top: -9px;
     border:0;
     border-bottom:5px solid #e67e22;
     border-left:5px solid transparent;
     border-right:5px solid transparent;
   }
   .puerto-menu-sub li ul li ul li:hover > ul {
     left:30px;
     min-width:150px;
     top: 35px;
   }
   .puerto-menu-sub li ul a {
     font-size:12px;
   }
   .puerto-menu-sub li ul a i {
     font-size:14px;
   }
  
  }
  


